.descriptionBlock {
    padding-left: 10px;
}

.name {
    color: #fdfdfd;
    font-size: 20px;
}
.avatar {
    max-width: 230px;
}
.inputWrapper {
    display: inline-block;
    position: relative;
}
.iconWrapper {
    position: absolute;
    top: -220px;
    right: 10px;
    cursor: pointer;
}
.inputFileIcon {
    max-width: 30px;
    opacity: 0.7;
}
.inputFile {
    opacity: 0;
    visibility: hidden;
    position: absolute;
}
.editButton {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 0 0;
    width: 230px;
    background-color: rgb(89, 158, 236);
    cursor: pointer;
    border-radius: 15px;
}
.editButton img {
    padding-left: 10px;
    max-width: 20px;
}

