.profileForm {
    display: inline-block;
    min-width: 100px;
}
span input {
    border-radius: 5px;
    border: 0 solid cornflowerblue;
}
.saveButton {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 0 0 0;
    width: 230px;
    background-color: rgb(89, 158, 236);
    cursor: pointer;
    border-radius: 15px;
}
.error {
    color: red;
}